import React from 'react';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Group,ListItem} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const News = (props,go,fetchedUser,Avatar,PropTypes) => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Новости
		</PanelHeader>
		<Group title="Фишки в интерфейсе">
			<ListItem>
				<p>
					В данном обновление были добавлены разделители у больших чисел в результате поиска, а также появилась новая вкладка "Новости" <br/><br/>
					Дата: 12.08.2019
				</p>
			</ListItem>
		</Group>
		

	</Panel>

);



export default News;
